@import url("https://fonts.googleapis.com/css2?family=M+PLUS+1+Code:wght@300&family=Poppins:wght@400;900&display=swap");
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  font-family: var(poppins-regular);
  color: var(p-color-1);
}

:root {
  --p-color-1: #111111;
  --p-color-2: #fbd758;
  --p-color-3: #fce38a;
  --p-color-4: #fdefbc;
  --p-color-5: #5e8c61;
  --p-color-6: #72bda3;
  --white: #fff;
}

:root {
  --break-max-mobile: 767px;
  --break-min-mobile: 375px;
  --break-tablet: 1023px;
  --break-tablet-landscape: 1024px;
  --break-min-desktop: 1199px;
  --break-medium-desktop: 1366px;
  --break-large-desktop: 1600px;
  --break-max-height-mobile: 500px;
  --break-orientation-portrait: portrait;
  --break-orientation-landscape: landscape;
}

.container {
  max-width: 1160px;
  padding-left: 32px;
  padding-right: 32px;
  margin: 0 auto;
}

pre,
code {
  background: rgba(var(--p-color-6), 1);
  color: var(p-color-1);
  direction: ltr !important;
  text-align: left !important;
  word-break: break-word;
  padding: 2px 5px;
  border-radius: 5px;
}

.skip-link {
  background: var(p-color-3);
  color: var(p-color-1);
  font-weight: 700;
  left: 50%;
  padding: 4px;
  position: absolute;
  transform: translateY(-250%);
}
.skip-link:focus {
  border: none;
}

.skip-link:focus {
  transform: translateY(250%);
  border: none;
}

.skip-link {
  /* Same as  before */
  transition: transform 0.3s;
}